.common-container {
  min-width: fit-content;
  flex: 1;
  box-sizing: border-box;
  border: 1px solid #ccc;
  margin-right: 8px;
  margin-bottom: 8px;
  overflow: auto;
  padding: 8px;
}
@media screen and (max-width: 746px) {
  .common-container {
    max-width: 360px;
  }
}
.counter-container,
.todo-container {
  min-width: 360px;
  display: flex;
}
.counter-container .counter-item,
.todo-container .counter-item {
  margin-right: 24px;
}
.counter-container .counter-text,
.todo-container .counter-text {
  display: inline-block;
  width: 24px;
  text-align: center;
}
.counter-container button,
.todo-container button {
  cursor: pointer;
}
.todo-container .todo-title {
  color: orange;
}
.todo-container .content {
  display: flex;
  flex-direction: column;
}
.contact-list {
  width: 360px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  margin-right: 8px;
  margin-bottom: 8px;
}
.contact-list dt,
.contact-list dd {
  margin: 0;
}
.contact-list dt {
  padding: 10px;
  font-weight: bold;
  background-color: #eee;
  cursor: pointer;
  border-bottom: 1px solid #ddd;
}
.contact-list dt:hover {
  background-color: #ddd;
}
.contact-list dd {
  display: none;
  padding: 10px;
  border-bottom: 1px solid #bbb;
}
.contact-list .expanded dd {
  display: block;
}
.contact-list .expanded .checked {
  background: green;
}
.context-container {
  width: 360px;
  min-width: fit-content;
  box-sizing: border-box;
  background-color: #eee;
  margin-right: 8px;
  margin-bottom: 8px;
  overflow: auto;
}
.context-container .context-item {
  padding: 0 8px;
}
